<template>
  <div>
    <div class="cart_top">
        <div class="cart_head">
            <van-icon name="arrow-left" size="0.6rem" @click="rigth_go " />
            <p class="cart_text">商品详情</p>
            <van-icon name="wap-nav" size="0.6rem" @click="navshow()" />
        </div>
            <ul class="cart-nav" v-if="flag">
                <footer>
                <li v-for="(foot,i) in foots" :key="i">
                    <router-link :to="{name:foot.name}">
                    <i class="iconfont" v-html="foot.icon"></i>
                    <span>{{ foot.txt }}</span>
                    </router-link>
                </li>
                </footer>
            </ul>
        </div>
        <div class="cart_img">
            <img :src="detail.imgs.img1" :alt="detail.productName">
             <p>{{detail.productName}}</p>
             <p style="color:#dd0f3d">￥{{detail.productPrice}}.00</p>
        </div>
        <div class="cart_text_nav">
           <p>
               {{detail.flowers}}
           </p>
           <p>
              {{detail.packaging}} 
           </p>
           <p>
                 {{detail.says}} 
           </p>
        </div>
        <div class="cart_tuwen">
          <p class="cart_tw_top">图文详情</p> 
          <p v-for="( img,i  ) in detail.details" :key="i" class="cart_tw_img" >
              <img :src="img" alt="">
          </p>
        </div>
        <div>
        <van-goods-action>
        <van-goods-action-icon icon="chat-o" text="客服" />
        <van-goods-action-icon icon="cart-o" text="购物车" @click="gotoCart" />
        <van-goods-action-icon icon="shop-o" text="店铺" />
        <van-goods-action-button type="warning" text="加入购物车" @click="addToshopcar"/>
        <van-goods-action-button type="danger" text="立即购买" @click="gotoCart"/>
        </van-goods-action>
        </div>
  </div>
</template>


<script>

import {getUsername} from "@/utils";
export default {
  name: "foot",

  data() {
    return {
      count:1, 
      detail:{},
      flag: false,
      foots: [
        { txt: "首页", path: "/index/home", name: "home", icon: "&#xe603;" },
        {
          txt: "分类",
          path: "/index/classify",
          name: "classify",
          icon: "&#xe605;"
        },
        { txt: "购物车", path: "/index/cart", name: "cart", icon: "&#xe606;" },
        { txt: "我的", path: "/index/mine", name: "mine", icon: "&#xe625;" }
      ]
    };
  },
  methods: {
    rigth_go() {
      this.$router.go(-1);
    },
    gotoHome() {
      this.$router.push({ name: "home" });
    },
    navshow() {
      this.flag = !this.flag;
    },
    gotoCart(){
         this.$router.push({ name: "cart" }); 
    },
    addToshopcar(){
            this.$axios.post("/vue/addShopCar",{
                username:getUsername(),
                goodId:this.$route.params.goodId,
                goodInfo:this.detail,
                
            }).then(res=>{
              
            })  
        },
  },
    mounted(){
        console.log(this.$route.params.goodId);

        this.$axios.get("/vue/getGoodDetail",{
            params:{
                goodId:this.$route.params.goodId
            }
        }).then(res=>{
            this.detail = res.data.result;
            
        })
    }
  
};
</script>

<style  scoped>
.cart_top {
  width: 100%;
  height: 0.8rem;
}
.cart_head {
  width: 100%;
  height: 0.8rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-weight: 400;
}
.cart_head > .van-icon {
  margin: 0 0.2rem;
  font-weight: 400;
}
.cart_text {
  flex: 1;
  text-align: center;
  font-size: 0.4rem;
}
.cart-nav {
  width: 1.2rem;
  height: 1.8rem;
  border: 1px solid #eee;
  position: absolute;
  top: 0.82rem;
  right: 0.4rem;
  border-radius: 5px;
  background: #fff;
  color: #666;
  font-size: 0.2rem;
  line-height: 0.38rem;
  text-indent: 0.15rem;
}
.cart_img{
    width: 100%;
    height: 8.8rem;
    background: #Fff;
}
.cart_img>img{
    width: 100%;
    height:7.5rem;

}
.cart_img>p{
    text-indent: 0.2rem;
    line-height: 0.6rem;
}

.cart_name{
width: 100%;
height: 1rem;
background: #Fff;  
}
.cart_text_nav{
    width: 100%;
    background: #fff;
    margin-top:0.6rem;
    font-size: 0.25rem;
    font-weight: 300;
    
}
.cart_text_nav>p{
    border-bottom: 1px solid #eee;
    padding: 0 0.2rem;
    
    line-height: 0.4rem;
}
.cart_tuwen{
    width: 100%;
    margin-top:0.5rem;
    margin-bottom: 2rem;
    background: #Fff;  
}
.cart_tuwen>p{
    width: 100%;
}
.cart_tw_top{
    width: 100%;
    height: 1rem;
    text-indent: 0.2rem;
    line-height: 1rem;
    font-size: 0.5rem;
}
.cart_tw_img>img{
    width: 100%;
}
</style>